	export default {
		data() {
			return {
				eyeImg1: false,
				popo:false,
				li:'',
				tid:'',
				jid:'',
				ajid:'',
				order_id:'',
				addressId:'',
				massagist_id:'',
				date:'',
				begin:'',
				end:'',
				value:'',
				free:'',
				can:'',
				selected: false,
				aa:'',
				times:'',
				remark:'',
				use_reduce:0,
				remarks: [],
				tagg:[
					{value:'可提前来'},
					{value:'准时服务'},
					{value:'来前联系'}
				],
				door_free:'',
				lidetail:''
			};
		},
		computed:{
			labels(){
				return this.remarks.join('，')
			}
		}, 
		onLoad(e){
			this.authKey = uni.getStorageSync('authKey');
			this.biId = e.id;
			this.orderD();
			this.orderR();
			this.orderCan();
			this.orderAdress();
		},
		onShow() {
			let jishi = uni.getStorageSync('jishi')
			let address = uni.getStorageSync('address')
			let date = uni.getStorageSync('date');
			let begin = uni.getStorageSync('begin');
			let end = uni.getStorageSync('end');
			let value = uni.getStorageSync('value');
	
			if (jishi){
				this.li = jishi
			} else {
				this.teach()
			}
			
			if (address){
				this.aa = address
			}
			
			if (value){
				this.date = date
				this.begin = begin
				this.end = end
				this.times = value
			}
		},
		methods:{
			// 技师信息
			teach(){
				uni.request({
					url: this.http+'home/myMassagists',
					method: 'GET',
					header: {
						authKey: this.authKey
					},
					data: {
					},
					success: res => {
						// console.log(res.data)
						if (res.data.code == 200) {
							let li = res.data.data[0]
							li.avatarurl = this.static + li.avatarurl
							this.li = li
							this.jid = res.data.data[0].id
						}
					},
				});
			},
			// 上门费
			orderD(){
				uni.request({
					url: this.http+'order/detail/' + this.biId,
					method: 'GET',
					header: {
						authKey: this.authKey
					},
					data: {
					},
					success: res => {
						// console.log(res.data)
						if (res.data.code == 200) {
							this.door_free = res.data.data.product.drop_in_fee
						}
					},
				});
			},
			// 免费上门次数
			orderR(){
				uni.request({
					url: this.http+'home/reduce',
					method: 'GET',
					header: {
						authKey: this.authKey
					},
					data: {},
					success: res => {
						// console.log(res.data)
						if (res.data.code == 200) {
							this.free = res.data.data
						}
					},
				});
			},	
			// 是否可选技师
			orderCan(){
				uni.request({
					url: this.http+'appointment/canichoose',
					method: 'GET',
					header: {
						authKey: this.authKey
					},
					data: {},
					success: res => {
						// console.log(res.data)
						if (res.data.code == 200) {
							this.can = res.data.data
						}
					},
				});
			},	
			// 地址
			orderAdress(){
				if(this.addressId){
					uni.request({
						url: this.http+'home/getAddressDetail/' + this.addressId,
						method: 'GET',
						header: {
							authKey: this.authKey
						},
						data: {
						},
						success: res => {
							// console.log(res)
							if (res.data.code == 200) {
								this.aa = res.data.data
							}
						},
					});
				}
			},
			remarkInput: function(e) {
				this.remark = e.target.value;
			},
			tag(e){
				if (this.remark.indexOf(e.value) < 0)
					this.remarks.push(e.value)
					this.remark += e.value
			},
			// 是否选择使用免费上门次数
			bindEye() {
				this.eyeImg1 = !this.eyeImg1;
				if(this.eyeImg1 == true){
					this.door_free = 0
					this.use_reduce = 1
				}else{
					this.use_reduce = 0
				}
			},
			// 预约
			tiji(){
				if (!this.aa){
					uni.showToast({
						title: '请填写上门地址',
						icon: 'none'
					})
					return
				}
				if (!this.times){
					uni.showToast({
						title: '请填写上门时间',
						icon: 'none'
					})
					return
				}
				var that= this
				this.date = this.date.replace("-","")
				uni.request({
					url: this.http+'appointment/create',
					method: 'POST',
					header: {
						authKey: this.authKey
					},
					data: {
						mode:2,
						order_id:this.biId,
						address_id:this.aa.id,
						massagist_id:this.li.id,
						date:this.date,
						begin:this.begin,
						end:this.end,
						remark:this.remark,
						use_reduce:this.use_reduce 
					},
					success: res => {
						// console.log(res.data)
						var datal = res.data.data
						this.order_id = datal.order_id
						// console.log(datal.order_id)
						// console.log(res.data.data.data)
						if (res.data.code == 200) {
							// 是否需要支付
							if(datal.paysign){
								uni.login({
								   	provider: 'weixin',
								    success(res){
								      if (res.code) {
								        wx.request({
								          url: 'https://zct.51kdd.com/index.php',
								          data: {
								            code: res.code,//要去换取openid的登录凭证
								          },
								          // method: 'GET',
								          success(res) {
								            console.log(res.data)
								   			console.log(datal,datal.timestamp)
								            wx.requestPayment({
								              timeStamp: datal.timestamp,
								              nonceStr: datal.noncestr,
								              package: datal.package,
								              signType: 'MD5',
								              paySign: datal.paysign,
								              success: function (res) {
								                // success
								                console.log(res);
												that.popo = true
										        uni.showToast({
										        	title:'支付成功',
										        	icon:'none',
										        	duration:1500
										        })
								              },
								              fail: function (res) {
								                // fail
												
									            uni.showToast({
									            	title:'支付失败',
									            	icon:'none',
									            	duration:1500
									            })
// 												uni.request({
// 													url: that.http+'appointment/paynotify/' + datal.order_id,
// 													method: 'GET',
// 													header: {
// 														authKey: that.authKey
// 													},
// 													data: {
// 													},
// 													success: res => {
// 														console.log(res.data)
// 														if (res.data.code == 200) {
// 															console.log('成功')
// 														}
// 													},
// 												});
									        },
								                complete: function (res) {
								                  // complete
								                  console.log(res);
								                }
								              })
								            }
								          })
								        } else {
								          console.log('获取用户登录态失败！' + res.errMsg)
								        }
								      }
								   });
								}else{
									this.popo = true
								}
						}else{
							uni.showToast({
								title:res.data.error,
								icon:'none',
								duration:1500
							})
						}
					},
				});
			},
			close(){
				this.popo = false
			},
			godetail(){
				 uni.navigateTo({
					url: '/pages/appointmentDetail/appointmentDetail?id=' + this.order_id
				});
			},
			goaddress(){
				 uni.navigateTo({
					url: '/pages/choose/address?id=' + this.biId +'&jid=' +this.ajid + '&sid=1'
				});
			},
			gotimes(){
				 uni.navigateTo({
					url: '/pages/choose/times?id=' + this.biId +'&jid=' +this.li.id + '&sid=1'
				});
			},
			gotech(){
				uni.navigateTo({
					url: '/pages/choose/technicians?id=' + this.biId + '&sid=1'
				});
			}
		}
	}
